.form__input {
  width: 100%;
  height: 6rem;
  padding: 0 5.6rem;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: #5f458c;
  border: 0;
  border-radius: 3rem;
  outline: none;
  transform: translateX(calc(100% - 6rem));
  transition: transform 0.7s ease-in-out;
}
.form__input:focus {
  border: 1px solid #a67ee5;
}
@media (min-width: 2000px) {
  .form__input {
    padding: 0 56px;
    height: 60px;
    font-size: 24px;
    border-radius: 30px;
    transform: translateX(calc(100% - 60px));
  }
}
@media (orientation: portrait) {
