    padding: 0 2.5rem;
    height: 4rem;
    font-size: 1.6rem;
    transform: translateX(calc(100% - 4rem));
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .form__input {
    margin-right: 1rem;
