@media (max-width: 1024px) and (orientation: portrait) {
  .footer__padding-y-8 {
    padding: 0 1rem;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .footer__padding-y-8 {
    padding: 0 1rem;
  }
}

@media (max-width: 1024px) and (orientation: portrait) {
  .footer__content {
    padding-left: 2.5rem;
    opacity: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .footer__content {
    padding-left: 2.5rem;
    opacity: 0;
  }
}

.footer__announce {
  display: none;
  margin-bottom: 1.6rem;
  padding: 3rem 0 0;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .footer__announce {
    display: block;
    opacity: 0;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .footer__announce {
    display: block;
    opacity: 0;
  }
}

.footer__button {
  padding: 0;
  margin: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  position: absolute;
  top: 0;
  z-index: 1;
  display: none;
  width: 3.5rem;
  height: 3.5rem;
  padding: 8px 8px 0 0;
  color: #5f458c;
}
.footer__button:focus {
  outline: none;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .footer__button {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .footer__button {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.footer__button--enlarge {
  left: 0;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .footer--full .footer__button--enlarge {
    opacity: 0;
    pointer-events: none;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .footer--full .footer__button--enlarge {
    opacity: 0;
    pointer-events: none;
  }
}

.footer__button--collapse {
  right: 0;
}
@media (max-width: 1024px) and (orientation: portrait) {
  .footer__button--collapse {
    opacity: 0;
    pointer-events: none;
  }
  .footer--full .footer__button--collapse {
    opacity: 1;
    pointer-events: auto;
  }
}
@media (max-width: 768px) and (orientation: landscape) {
  .footer__button--collapse {
    opacity: 0;
    pointer-events: none;
  }
  .footer--full .footer__button--collapse {
    opacity: 1;
    pointer-events: auto;
  }
}

.screen__footer-label {
  margin: 0 0 0.3rem;
  text-transform: uppercase;
  font-size: 1.6rem;
  line-height: 1.2;
}

.screen__footer-date {
  margin: 0;
  font-family: "Radical", "Arial", sans-serif;
  font-weight: 400;
  font-size: 2.4rem;
  line-height: 0.9;
}